)]),_c('p',[_vm._v("La pérdida de agua tras la cosecha afecta la consistencia del tejido de frutas y verduras, lo que resulta en una disminución de su calidad sensorial. Esto se traduce en cambios negativos en su aroma, brillo y sabor. A medida que se activa el metabolismo de degradación, la velocidad de respiración del producto aumenta considerablemente, lo que lleva a un consumo acelerado de oxígeno. Este proceso provoca la pérdida de azúcares y otros compuestos esenciales que son cruciales para mantener el aroma y el sabor característicos de los alimentos.")])]),_c('div',{staticClass:"col-md-8 col-xl-5 mb-3"},[_c('figure',[_c('img',{staticClass:"mx-auto",attrs:{"src":require("@/assets/curso/temas/tema2/img_12.png"),"alt":"Texto que describa la imagen"}})])])])]),_c('div',{staticClass:"tarjeta bg-01 p-2"},[_c('div',{staticClass:"row ordenar_tarjeta justify-content-center align-items-center p-3"},[_c('div',{staticClass:"col-md-12 col-xl-7 mb-4"},[_c('h3',{staticClass:"mb-2"},[_vm._v("Deterioro enzimático:")]),_c('p',[_vm._v("La ruptura de los tejidos vegetales, ya sea por daños accidentales o durante el proceso de pelar una fruta, provoca desorganización de las enzimas naturales contenidas en las células. Este daño afecta las membranas celulares, lo que resulta en la liberación de enzimas y sustratos que desencadenan reacciones incontrolables. Como consecuencia, se pierde tanto la calidad sensorial del producto, incluyendo su sabor y aroma, como su valor nutricional, lo que impacta negativamente en la experiencia del consumidor.")])]),_c('div',{staticClass:"col-md-8 col-xl-4 mb-3"},[_c('figure',[_c('img',{staticClass:"mx-auto",attrs:{"src":require("@/assets/curso/temas/tema2/img_13.png"),"alt":"Texto que describa la imagen"}})])])])])])],1)])])])],1)],1)
